.App {
  background-color: black;
  height: 100vh;
  width: 100vw;
  position: relative;

  overflow: hidden;
}

.images_wrapper {
  display: grid;
  gap: 0px;
  max-height: 100%;
  max-width: 100%;
  width: calc(100% - 100px);
  height: calc(100% - 100px);
  padding: 50px;
}

.flex_container {
  /* flex: 1; */
  position: relative;
}

.image_preview {
  position: absolute;
  opacity: 0;
  transition: 2s opacity ease-in;
}

.image_preview.visible {
  display: block;
  opacity: 100;
}

.image_preview.portrait {
  height: 110%;
}

.image_preview.landscape {
  width: 110%;
}

.image_preview img {
  width: auto;
  height: auto;

  max-width: 110%;
  max-height: 110%;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.image_preview .imageNameLine {
  padding: 10px;
  background-color: rgba(50,50,50,0.5);

  position: absolute;
  bottom: 0;
  right: 0;
}

.dev_panel {
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 100000;

  background-color: rgba(200,200,200,0.4);
}